
#app .wrap{
   height: 610px;
  	width: 100;
	  background-image: url(../../assets/LoginBackground1.jpg);
 	background-repeat: no-repeat;
   background-size: cover;
  	/* background-position: center center; */
  	position: relative;
}
#wrap .logGet {
 	height: 408px;
 	width: 368px;
 	position: absolute;
 	background-color: #FFFFFF;
 /*  filter:alpha(Opacity=80);-moz-opacity:0.5;opacity: 0.5; */
 	top: 30%;
 	right: 10%;
}
.logC button {
 	width: 100%;
 	height: 45px;
 	background-color: #ee7700;
 	border: none;
 	color: white;
 	font-size: 18px;
}
.logGet .logD.logDtip .p1 {
 	display: inline-block;
 	font-size: 28px;
 	margin-top: 30px;
 	width: 86%;
}
#wrap .logGet .logD.logDtip {
 	width: 86%;
 	border-bottom: 1px solid #ee7700;
 	margin-bottom: 60px;
 	margin-top: 0px;
 	margin-right: auto;
 	margin-left: auto;
}
.logGet .lgD img {
 	position: absolute;
 	top: 12px;
 	left: 8px;
}
.logGet .lgD input {
 	width: 100%;
 	height: 42px;
 	text-indent: 2.5rem;
}
#wrap .logGet .lgD {
 	width: 86%;
 	position: relative;
 	margin-bottom: 30px;
 	margin-top: 30px;
 	margin-right: auto;
 	margin-left: auto;
}
#wrap .logGet .logC {
 	width: 86%;
 	margin-top: 0px;
 	margin-right: auto;
 	margin-bottom: 0px;
 	margin-left: auto;
}




